/**
 * @fileoverview Authentication Server Bridge
 * @module identity-access/components/AuthServerBridge
 * 
 * Server component that reads authentication cookies and bridges them
 * to client-side token management for production authentication.
 */

import { cookies } from 'next/headers';
import { AuthTokenSync } from './AuthTokenSync';
import { COOKIE_NAMES } from '@/identity-access/lib/config/cookies';

/**
 * AuthServerBridge Component
 * 
 * Server component that reads authentication tokens from HTTP-only cookies
 * and passes them to the client-side AuthTokenSync component for secure
 * client-side token management.
 */
export async function AuthServerBridge() {
  try {
    const cookieStore = await cookies();
    
    // Get tokens from server-side cookies
    const accessToken = cookieStore.get(COOKIE_NAMES.ACCESS_TOKEN)?.value;
    const refreshToken = cookieStore.get(COOKIE_NAMES.REFRESH_TOKEN)?.value;
    
    // If no access token, don't render sync component
    if (!accessToken) {
      return null;
    }

    // TODO: In production, decode user data from JWT token or fetch from session
    // For now, we'll let the AuthTokenSync handle token storage without user data
    // The AuthProvider will manage user state through Redux
    
    return (
      <AuthTokenSync 
        accessToken={accessToken}
        refreshToken={refreshToken}
      />
    );
  } catch (error) {
    console.error('[AuthServerBridge] Failed to read auth cookies:', error);
    return null;
  }
}